<template>
  <div>
    <div class="loginContainer">
      <div class="title">会员登录</div>
      <div>
        <div class="item tel" :class="stateTel">
          <span></span>
          <div>
            <mt-field
              type="text"
              placeholder="请输入手机号"
              v-model="tel"
              :attr="{ maxLength: 11 }"
              @blur.native.capture="checkTel"
            ></mt-field>
          </div>
        </div>
        <div class="item pwd" :class="statePassword">
          <span></span>
          <div>
            <mt-field
              type="password"
              placeholder="请输入密码"
              v-model="password"
              @blur.native.capture="checkPassword"
            >
            </mt-field>
          </div>
        </div>
        <div class="item tyzm" :class="stateTyzm">
          <span></span>
          <div>
            <mt-field
              type="text"
              placeholder="请输入图形验证码"
              v-model="tyzm"
              @blur.native.capture="checkTyzm"
            >
              <div>
                <img src="../assets/imagecode.png" />
                <p>看不清换一个</p>
              </div>
            </mt-field>
          </div>
        </div>

        <mt-button type="default" size="large" @click.native.capture="submit"
          >登录</mt-button
        >
        <div class="item">
          没有账号？<router-link to="/register">立即注册</router-link>
        </div>
      </div>
    </div>
    <my-footer :tabActive="tabActive">></my-footer>
  </div>
</template>
<script>
export default {
  data() {
    return {
      // 手机号
      tel: "",
      stateTel: "",

      // 密码
      password: "",
      statePassword: "",

      // 图片验证码
      stateTyzm: "",
      tyzm: "",
      tabActive: "index",
    };
  },
  methods: {
    // ...mapMutations(["login"]),
    // 手机验证
    checkTel() {
      var telRegExp = /^1[3578]\d{9}$/;
      if (!telRegExp.test(this.tel)) {
        this.stateTel = "error";
        return false;
      }
      this.stateTel = "";
      return true;
    },

    // 密码验证
    checkPassword() {
      var passwordRegExp = /^[a-zA-Z0-9_]{6,20}$/;
      if (!passwordRegExp.test(this.password)) {
        this.statePassword = "error";
        return false;
      }
      this.statePassword = "";
      return true;
    },
    // 图片验证码验证
    checkTyzm() {
      if (this.tyzm == "") {
        this.stateTyzm = "error";
        return false;
      }
      this.stateTyzm = "";
      return true;
    },
    // 提交表单验证
    submit() {
      if (this.checkTel() && this.checkPassword() && this.checkTyzm()) {
        // this.$store.commit("login", );
        // this.login({ username: "duoduo", tel: this.tel });
        // localStorage.setItem("name", this.tel);
        localStorage.setItem("tel", this.tel);
        localStorage.setItem("islogined", true);

        if (this.$route.query.path) {
          this.$router.push(this.$route.query.path);
        } else {
          this.$router.push("/");
        }
        // ajax登录开始
        // var data = {
        //   tel: this.tel,
        //   password: this.password,
        //   tyzm: this.tyzm,
        // };
        // this.axios.post("/login", this.qs.stringify(data)).then((res) => {
        //   console.log(res);
        //   if (res.data.code == 201) {
        //     this.$messagebox.alert("注册失败", "注册提示");
        //   }
        //   if (res.data.code == 200) {
        //     this.$router.push("/");
        //   }
        // });
        // ajax登录结束;
      }
    },
  },
  created() {
    document.documentElement.scrollTop = 0;
  },
};
</script>
<style>
.loginContainer {
  background-color: #2281d5;
  height: 50rem;
}
.loginContainer .title {
  background-color: #f2f2f2;
}

.loginContainer > div:nth-child(2) {
  padding: 0 1rem;
  color: #fcfcfc;
  margin-top: 1.5rem;
  padding-bottom: 1rem;
}

.loginContainer button {
  color: #2281d5;
}

.loginContainer .item {
  display: flex;
  margin: 0.5rem auto;
}

.loginContainer .item > span {
  display: block;
  background: url(../assets/register_bg.png) no-repeat -7% center #69a9e2;
  width: 3rem;
  background-size: 500%;
}

.loginContainer .tel > span {
  background-position: 49% 49%;
}
.loginContainer .tyzm > span {
  background-position: 78% 60%;
}

.loginContainer .item > div {
  flex: 1;
}

.loginContainer .tyzm p {
  font-size: 0.2rem;
  margin-top: 0.2rem;
}
.loginContainer .tyzm img {
  width: 4.3rem;
  height: 1rem;
}
.loginContainer .item:last-child a {
  color: #fcfcfc;
}
</style>
